import "./burger.scss";

import { NavLink } from "react-router-dom";

const Burger = (props) => {
  let active = {
    color: "rgb(81 200 245)",
  };
  let disactive = {
    color: "black",
  };
  return (
    <div className="burger">
      <div className="nav-burger">
        <NavLink
          to="/development"
          style={({ isActive }) => (isActive ? active : disactive)}
          onClick={props.scrollUp}
        >
          Розробка
        </NavLink>

        <NavLink
          to="/support"
          style={({ isActive }) => (isActive ? active : disactive)}
        >
          Підтримка
        </NavLink>

        <NavLink
          to="/portfolio"
          style={({ isActive }) => (isActive ? active : disactive)}
          onClick={props.scrollUp}
        >
          Портфоліо
        </NavLink>

        <NavLink
          to="/contact"
          style={({ isActive }) => (isActive ? active : disactive)}
          onClick={props.scrollUp}
        >
          Контакти
        </NavLink>
      </div>
    </div>
  );
};

export default Burger;
